<template>
	<div class="equity-register">
		<div class="form">
			<p>
				<span>账户地址</span>
				<span class="special">{{address}}</span>
			</p>
			<p>
				<span>股权登记份数</span>
				<span class="height input-special">
                    <input type="number" placeholder="请输入本次股权登记份数" v-model="input" @input="inputFunc">
                    <i v-if="visible" @click='closeX'>X</i>
                </span>
			</p>
			<p v-if="error" class="error">登记股数不可超过总流通股数：{{ value }},且不可为0</p>
			<p v-if="errorInt" class="error">请输入整数</p>
		</div>
		<div class="btn">
			<button @click='sure' v-loading="loading" :disabled="sureDisabled">确定登记</button>
		</div>

		<div class="modal" v-if="success">
			<div class="modal-main">
				<div class="modal-content">
					<span>
						登记成功！
					</span>
				</div>
				<div class="model-bottom" @click='sureUser'>
					确定
				</div>
			</div>
		</div>

		<div class="modal" v-if="fail">
			<div class="modal-main">
				<div class="modal-content fail-content">
					<span>
						登记失败！
					</span>
				</div>
				<div class="model-bottom fail-bottom" @click='failUser'>
					确定
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	//import  from ''
	import ContractServies from '@/services/contract-servies';
	import ContractConfig from '@/config/contract-config';
	export default {
		//组件名
		name: 'template',
		//实例的数据对象
		data() {
			return {
				input: "",
				visible: false,
				error: false,
				address: "",
				value: '',
				loading: false,
				success: false,
				fail: false,
				errorInt:false,
				sureDisabled: false,
			}
		},
		//数组或对象，用于接收来自父组件的数据
		props: {

		},
		//计算
		computed: {

		},
		//方法
		methods: {
			sureUser() {
				this.success = false;
			},
			failUser() {
				this.fail = false;
			},
			inputFunc() {
				console.log(this.input)
				// this.visible=true;
				if(this.input) {
					this.visible = true;
					if(parseInt(this.input) > parseInt(this.value)) {
						this.error = true;
					} else {
						this.error = false;
					}
				} else {
					this.visible = false;
				}
			},
			closeX() {
				this.input = '';
				this.visible = false;
				this.error = false;
			},
			//确定过户
			sure() {
				if(/^-?\d+$/.test(this.input)){
					this.errorInt = false;
					if(0 >= parseInt(this.input) || parseInt(this.input) > parseInt(this.$route.query.balance)) {
						this.error = true;
					} else {
						this.error = false;
						this.loading = true;
						this.sureDisabled = true;
						let pbAPubKey = ContractServies.getpublicKey(), //转出公钥
							pbBPubKey = this.$route.query.publicKey, //转入公钥
							pbAPriKey = ContractServies.getprivateKeyRandom(); //转出私钥
						//转出账户的余股密文
						//返回证明
						var obj = {
							pbBalAPubCipher: this.$route.query.cipherText,
							pbAPubKey: pbAPubKey,
							pbBPubKey: pbBPubKey,
							pbAPriKey: pbAPriKey,
							pbBalance: this.$route.query.balance,
							pbTrade: this.input,
						};
						Was.exec('nizk', 'nizkGenProof', obj, (res) => {
							console.log(JSON.stringify(res));
							if(res.code == 0) {
								console.log('证明结构' + res.data);
								let data = JSON.parse(res.data),
									prove = data.nizkProof, //证明
									pbTraAPubCipher = data.pbTraAPubCipher, //转出金额密文
									pbTraBPubCipher = data.pbTraBPubCipher; //转入金额密文
								console.log('返回的证明' + prove);
								console.log('账户余股的密文' + obj.pbBalAPubCipher);
								console.log('转出金额密文' + pbTraAPubCipher);
								console.log('转入金额密文' + pbTraBPubCipher);
								console.log('转出账户的公钥' + pbAPubKey);
								console.log('转入账户的公钥' + pbBPubKey);
								console.log('转出账户的私钥' + ContractServies.getprivateKeyRandom());
								//发送交易
								var param = {
									cipher1: "11111", //第一个加数/减数（密文）
									cipher2: "1111", //第二个加数/减数（密文）
									pais: prove, //证明的加密密文
									balapubcipher: obj.pbBalAPubCipher, //账户余股的密文
									traapubcipher: pbTraAPubCipher, //转出账户需要转出的金额密文
									trabpubcipher: pbTraBPubCipher, //转入账户需要转入的金额密文
									apukkey: pbAPubKey, //转出账户的公钥
									bpukkey: pbBPubKey, //转入账户的公钥
									nizkpp: "" //零知识证明的全局结构
								};
								ContractServies.sendRawTrasaction('JuzixTokenManager', 'buyFrom', [param, this.address], '', (ret, data) => {
									console.log('ret:::::::::', ret);
									if(ret == 0) {		
										//									this.success = true;
										//跳转回首页
										this.$router.push({
											path: '/welcome',
										});
									} else {
										this.fail = true;
									}
									this.loading = false;
									this.sureDisabled = false;
								});
							} else {
								this.sureDisabled = false;
								this.loading = false;
								this.fail = true;
							}
						});
					}
				}else{
					this.errorInt = true;
				}


			}
		},
		//生命周期函数
		created() {
			console.log('账户地址' + this.$route.query.address);
			console.log('余额密文' + this.$route.query.cipherText);
			console.log('轉入公鑰' + this.$route.query.publicKey);
			this.address = this.$route.query.address;
			this.value = this.$route.query.balance;
		},
		beforeMount() {},
		mounted() {

		},
		//监视
		watch: {

		},
		//组件
		components: {

		},
		//过滤器
		filters: {

		},
		//自定义指令
		directive: {

		}
	}
</script>

<!--
	作者：liangyanxiangde@163.com
	时间：2017-03-27
	描述：统一使用less,局部样式
-->
<style lang="less" scoped>
	@import "../../less/modules/base.less";
	@import "../../less/modules/color.less";
	@height: 1.20rem;
	.form {
		margin-bottom: 1rem;
	}

	p {
		// height:@height;
		border-bottom: .01rem solid @border3;
		// line-height: @height;
		padding: 0 .30rem 0 .50rem;
		display: flex;
		flex-wrap: nowrap;
		span {
			display: inline-block;
			color: #000;
			&:last-child {
				width: 4.30rem;
				word-wrap: break-word;
				word-break: break-all;
			}
			&:first-child {
				width: 3rem;
				height: @height;
				line-height: @height;
				margin-right: .20rem;
				font-size: .28rem;
			}
		}
		.height {
			line-height: @height;
			input {
				display: inline-block;
				border: 0;
				height: 90%;
				background: none;
				width: 100%;
				font-size: .24rem;
				outline: none;
			}
		}
		.special {
			margin-top: 30rem/@base;
		}
		.input-special {
			position: relative;
			i {
				display: inline-block;
				width: .60rem;
				position: absolute;
				right: 0;
				top: 0;
				font-style: normal;
				font-size: .30rem;
				text-align: center;
			}
		}
	}

	.error {
		float: right;
		border-bottom: none;
		color: red;
		line-height: .80rem;
	}

	.btn {
		padding: 0 .30rem;
		text-align: center;
		button {
			width: 6.60rem;
			height: .80rem;
			line-height: .80rem;
			background: #0a8bec;
			border-radius: .12rem;
			border: none;
			color: #fff;
			font-size: .30rem;
			text-align: center;
		}
	}

	.modal {
		position: fixed;
		top: 0;
		left: 0;
		z-index: 999;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.5);
		.modal-main {
			//弹窗主体
			position: absolute;
			// width:50%;
			top: 50%;
			left: 50%;
			-webkit-transform: translate(-50%, -50%);
			transform: translate(-50%, -50%);
			background-color: #fff;
			border-radius: .18rem;
			.modal-title {
				//弹窗标题
				height: 1rem;
				line-height: 1rem;
				text-align: center;
				border-bottom: .01rem solid @border3;
			}
			.modal-content {
				padding: .5rem 1rem .2rem;
				font-size: .3rem;
				border-bottom: .02rem solid @border3;
			}
			.fail-content {
				color: red;
			}
			.model-bottom {
				font-size: .28rem;
				text-align: center;
				height: 1rem;
				line-height: 1rem;
				color: #0383ff;
			}
			.fail-bottom {
				color: #ccc;
			}
		}
	}
</style>


